.contact-container {
  height: 80vh;
  background-color: #6ddb46;
  margin-top: 10%;
  display: flex;
  width: 100%;
  position: relative;
}

.left-contact-part {
  width: 50%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  padding-left: 4%;
  /* background-color: grey; */
}

.left-part-upper-section {
  font-family: Lato;
  font-weight: 400;
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.left-upper-button {
  background-color: white;
  width: 15%;
  text-align: center;
  padding: 10px;
  border-radius: 5px;
  color: #7c7c7c;
  cursor: pointer;
  transition: 0.3s ease;
}
.left-upper-button:hover {
  color: white;
  background-color: #236f00;
}
.email-input {
  width: 80px;
}
.left-upper-heading {
  font-size: 30px;
}
.left-upper-sub-heading {
  font-size: 15px;
  margin-top: 5%;
}

.left-part-lower-section {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 30%;
  font-family: Lato;
}
.left-lower-sub-heading {
  margin-top: 2%;
}
.left-lower-call {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  margin-bottom: 2%;
}
.left-lower-email {
  display: flex;
  /* justify-content: center; */
  align-items: center;
}
.call-image {
  margin-right: 1%;
  background-image: url(../images/call-image.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 20px;
  height: 20px;
}
.email-image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin-right: 1%;
  background-image: url(../images/email-image.png);
  width: 20px;
  height: 20px;
}
.rigth-contact-part {
  width: 50%;
  /* background-color: black; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-email {
  height: 75%;
  width: 75%;
  border-radius: 5px;
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
.input-box {
  width: 100%;
}
.email-input {
  border-radius: 4px;
  background-color: #f5f5f5;
  border: none;
  width: 80%;
  outline: none;
  line-height: 2.2;
  padding: 0px 7px 0px 7px;
  box-sizing: border-box;
}
.text-area-input {
  border-radius: 4px;
  background-color: #f5f5f5;
  border: none;
  width: 80%;
  height: 30%;
  outline: none;
  line-height: 2.2;
  padding: 0px 7px 0px 7px;
  box-sizing: border-box;
}

.contact-end-info {
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;

  color: #939393;
  font-family: Lato;
}
.contact-email-heading {
  margin-left: 10%;
  align-self: start;
  font-family: Lato;
  font-size: 20px;
}
.contact-email-sub-heading {
  margin-top: 3%;
  font-size: 14px;
}
.send-button {
  margin-left: 10%;
  align-self: start;
  background-color: #418a1e;
  padding: 10px 15px 10px 15px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}
.send-button:hover {
  color: black;
  background-color: white;
  border: 1px solid black;
}

#tell-no:hover {
  color: white;
}

@media screen and (max-width: 1190px) {
  .left-upper-button {
    width: 35%;
  }
}

@media screen and (max-width: 768px) {
  .contact-container {
    height: auto;

    width: 100%;
    position: relative;
    flex-direction: column;
  }

  .left-contact-part {
    width: 100%;
    height: 60vh;
    padding: 0;
  }
  .left-upper-heading {
    font-size: 20px;
  }

  .left-upper-sub-heading {
    font-size: 12px;
  }
  .left-upper-button {
    font-size: 12px;
    width: 20%;
    margin-top: 2%;
  }

  .left-part-lower-section {
    font-size: 13px;
    padding-left: 4%;
  }
  .left-part-upper-section {
    padding-left: 4%;
  }
  .rigth-contact-part {
    width: 100%;

    height: 60vh;
    order: -1;
  }

  .contact-email {
    height: 90%;
    width: 90%;
  }
  .contact-email-sub-heading {
    font-size: 12px;
  }
  .contact-end-info {
    font-size: 12px;
    text-align: center;
  }
  .contact-info {
    margin-bottom: 2%;
  }
}
