.about-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.family-image {
  background-image: url(../images/family.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: calc(100vh - 65px);
  width: 95%;
}
.about-layer {
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  width: 95%;
  height: calc(100vh - 65px);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.about-layer-text,
.about-layer-subtext {
  color: white;
  font-size: 50px;
  font-family: Lato;
}
.about-layer-subtext {
  margin-top: 2%;
  font-size: 30px;
}

.about-heading {
  font-size: 40px;
  margin-top: 10%;
  font-family: Lato;
}
.about-info {
  font-family: Lato;
  margin-top: 5%;
  font-size: 17px;
  width: 95%;
  text-align: center;
  line-height: 30px;
}
.about-video {
  margin-top: 5%;
  margin-bottom: 2%;
}
@media screen and (max-width: 768px) {
  .about-layer-text {
    font-size: 30px;
  }
  .about-info,
  .about-layer-subtext {
    font-size: 13px;
    line-height: 25px;
  }
  .about-heading {
    font-size: 20px;
  }
}
