.home-logo {
  /* padding: 20px 30px 20px 30px; */
  background-image: url(../images/wallyn-home.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 250px;
  width: 450px;
}

.video-bg {
  /* position: relative; */
  width: 100%;
  height: 50vh;
  object-fit: cover;
}
.filter-bar-container {
  position: absolute;
  top: calc(60vh+10px);
  right: 5%;
  width: 10%;
}
.filter-bar {
  transition: 1s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 4.5vh;
  /* float: right; */

  background-color: #f5f5f5;

  padding: 10px;

  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  cursor: pointer;
}

.filter-image,
.filter-image-arrow {
  transition: 0.3s ease;
  background-image: url(../images/Arrow.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 15px;
  width: 17px;
}
.filter-image {
  background-image: url(../images/filter.png);
  height: 25px;
  width: 29px;
}
.drop-down-list {
  transition: 0.3s ease;
  /* height: 10vh; */
  border: 1px solid;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  position: relative;
  z-index: 1;
}
.drop-down-list > li {
  width: 100%;
  text-align: center;
  cursor: pointer;
  padding: 10px;
}
.drop-down-list > li:hover {
  background-color: green;
  color: white;
}
.fade-out {
  animation: fadeOut 0.5s ease-out;
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.layer {
  background-color: rgba(105, 244, 63, 0.5);

  width: 100%;
  height: 50vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.home-heading {
  font-family: Lato;
  font-size: 50px;
  margin-bottom: 20px;
  text-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5);
}
.home-sub-heading {
  font-size: 30px;
  font-family: Lato;
  margin-top: 20px;
  text-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5);
}
.layer-text {
  font-family: Lato;
  text-align: center;
  color: white;

  z-index: 1;
}

.layer .call-button {
  font-family: Poppins;
  font-size: 18px;
  background-color: white;
  border-radius: 5px;
  padding: 10px;
  margin-top: 40px;
  cursor: pointer;
  z-index: 1;
}
.layer .call-button:hover {
  color: white;
  background-color: green;
}

.video {
  margin-bottom: 20px;
}

.cars-heading {
  margin-top: 10%;
  margin-bottom: 5%;
  font-size: 40px;
  text-align: center;
  font-family: Lato;
}

.skeleton-cards-container {
  display: grid;
  /* width: 100%;
  height: 30%; */
  /* height: auto; */

  justify-content: space-around;

  grid-template-columns: repeat(auto-fit, minmax(350px, 420px));
}
.skeleton-cards {
  /* cursor: pointer; */
  margin-bottom: 10%;
  height: 450px;
  /* border-radius: 5px; */
  /* background-color: #f5f5f5; */
  /* box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
  /* overflow: hidden; */
}
.skeleton-title,
.skeleton-info,
.skeleton-image {
  height: 3vh;
  width: 95%;
  background-color: #9e9e9e;
  border-radius: 3px;
  margin-top: 2%;
  margin-left: 1%;
}
.skeleton-info {
  width: 60%;
  background-color: #bebebe;
}
.skeleton-image {
  width: 100%;
  height: 70%;
  margin: 0;
  border-radius: 0;
  background-color: #bebebe;
}
.skeleton-card {
  margin-top: 5%;
  /* width: 400px; */
  height: 400px;
  background-color: #e5e5e5;
  border-radius: 4px;
  animation: skeleton-anim 1s ease infinite alternate;
}

@keyframes skeleton-anim {
  0% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 768px) {
  .home-logo {
    height: 125px;
    width: 250px;
  }
  .skeleton-card {
    height: 400px;
    margin-left: 2%;
    margin-right: 2%;
  }
  .home-heading {
    font-size: 40px;
  }

  .filter-bar-container {
    width: 30%;
  }
  .call-button {
    font-size: 15px;
  }
  .filter-image,
  .filter-image-arrow {
    height: 10px;
    width: 12px;
  }
  .filter-image {
    width: 15px;
    height: 18px;
  }
  .cars-heading {
    margin-top: 30%;
  }

  .drop-down-list {
    font-size: 13px;

    background-color: white;
  }
}
