.scroll-card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60vh;
  position: relative;
}

.scroll-car-wrapper {
  /* width: 100%;
  background-color: rgb(48, 48, 48);
  height: 70vh;
  position: relative; */
}
.scroll-card {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  flex-direction: column;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  /* transform: translateX(25%); */
  transition: 0.3s ease;
  scroll-snap-type: y mandatory;
}

.scroll-image {
  scroll-snap-align: start;
  background-image: url(https://autos-motos.net/pictures/BE478268990/14309/image-01-B~v1622644578.JPG);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-width: 50%;
  min-height: 100%;
  transform: scale(0.95);
  transition: 0.3s ease;
  opacity: 0.5;
  cursor: pointer;
  /* margin-bottom: 10px; */
}
.left-part-scroll {
  width: 50%;
}
.next {
  background-color: grey;
  position: absolute;
  right: 10px;
  cursor: pointer;
}
.prev {
  background-color: grey;
  position: absolute;
  left: 10px;
  cursor: pointer;
}
#active {
  opacity: 1;
}

.arrow-scroll-anime {
  position: absolute;
  z-index: 1;
  top: 50%;
  background-image: url(../images/arrow-scroll.png);
  background-position: center;
  background-size: contain;
  width: 40px;
  height: 50px;

  animation: scroll-anime 1s ease infinite alternate;
  background-repeat: no-repeat;
}

@keyframes scroll-anime {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
    transform: translateY(20px);
  }
}
@media screen and (max-width: 1100px) {
  .scroll-image {
    min-width: 73%;
  }
}
@media screen and (max-width: 768px) {
  .scroll-image {
    min-width: 90%;
    transform: scale(1);
  }
}
