.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.burger {
  display: none;
}
.navbar-container .logo {
  /* padding: 20px 30px 20px 30px; */
  background-image: url(../images/wallyn-logo1.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 50px;
  width: 93px;
  margin-left: 25px;
  margin-top: 5px;
}
.link-list {
  display: flex;
  justify-content: space-around;
  /* background-color: grey; */
  align-items: center;
}
.navbar-container .link-list .links {
  /* margin: 20px; */
  font-family: Poppins;
  font-size: 16px;
  padding: 20px 30px 20px 30px;
  cursor: pointer;
  transition: 0.5s ease;
}
.navbar-container .link-list .links:hover {
  background-color: green;
  color: white;
}

@media screen and (max-width: 768px) {
  /* .navbar-container {
    align-items: flex-end;
  } */
  .navbar-container .link-list a {
    width: 100%;
  }
  .links {
    padding: 30px 0;
    /* width: 100%;รทร */
    text-align: center;
  }
  .nav-elements {
    position: absolute;
    height: calc(100vh - 55px);
    top: 55px;
    width: 100%;

    overflow-x: hidden;
  }
  .link-list {
    flex-direction: column;
    position: absolute;
    justify-content: center;
    height: 100%;
    width: 100%;
    background: #6ddb46;
    z-index: 2;
    transform: translateX(100%);
    transition: ease-in 0.3s;
  }
  .burger {
    z-index: 3;
    display: block;
    position: absolute;
    top: 15px;
    right: 25px;
    cursor: pointer;
  }
  .burger div {
    z-index: 100;
    width: 20px;
    height: 1px;
    background-color: black;
    margin: 5px;
    transition: ease-in 0.3s;
  }
  .cross .Line1 {
    /* background-color: white; */
    transform: rotate(45deg) translate(0px, 8.5px);
  }
  .cross .Line2 {
    opacity: 0;
  }
  .cross .Line3 {
    /* background-color: white; */
    transform: rotate(-45deg) translate(0px, -8.5px);
  }
}
