@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap");

.car-cards-container {
  display: grid;
  /* width: 100%;
  height: 30%; */
  /* height: auto; */
  justify-content: space-around;
  animation: load 1s ease-in;
  grid-template-columns: repeat(3, minmax(350px, 400px));
  /* grid-template-columns: repeat(3, minmax(350px, 400px)); */
  grid-gap: 23px;
}
#grid-two {
  grid-template-columns: repeat(2, minmax(350px, 400px));
}
#grid-one {
  grid-template-columns: repeat(1, minmax(350px, 400px));
}
.car-cards {
  cursor: pointer;
  margin-bottom: 10%;
  height: 400px;

  /* border-radius: 5px; */

  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25);
  overflow: hidden;
}

.card-image {
  /* background-image: url(https://autos-motos.net/pictures/BE478268990/14309/image-01-B~v1622644578.JPG); */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 75%;
  transition: 0.3s ease;
  position: relative;
}
.card-image:hover {
  transform: scale(1.02);
}
a {
  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}
.card-text span {
  font-weight: 400;
  font-size: 15px;
  color: rgb(89, 89, 89);
}
.card-text {
  font-family: Lato;
  font-weight: 900;
  font-size: 18px;
  /* background-color: grey; */
  padding-left: 20px;

  padding-top: 10px;
}

.car-cards ul {
  font-family: Lato;
  font-weight: 400;
  display: flex;
  /* box-shadow: 0px -1px black; */
  /* list-style-type: disc; */
  justify-content: space-around;
  margin-top: 2%;
  font-size: 13px;
  padding-top: 8px;
}

.car-cards ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.mid-list {
  box-shadow: 1px 0px black, -1px 0px black;
}
.car-cards ul li .car-date-image,
.car-cards ul li .car-km-image,
.car-cards ul li .car-petrol-image {
  height: 20px;
  min-width: 36%;
}

.money {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: rgb(0, 0, 0, 0.8);
  color: white;
  padding: 10px;
}
@keyframes load {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 768px) {
  .car-cards {
    height: 400px;
    margin-left: 2%;
    margin-right: 2%;
  }
  .card-text {
    font-size: 13px;
  }
  .money {
    font-size: 16px;
  }
}
