.contact-full-page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contact-full-page-header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #f5f5f5;
  width: 95%;
  height: 40vh;
  font-size: 40px;
  font-family: Lato;
}
.contact-full-page-sub-header {
  font-size: 20px;
  margin-top: 2%;
  color: #7c7c7c;
}

.contact-full-page-profile {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 95%;
  /* height: 30vh; */
  margin-top: 5%;
}
.contact-full-page-father {
  background-image: url("../images/profile-pic.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: grey;
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.left-image,
.right-image {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  width: 50%;
  height: 35vh;
}

.post,
.name {
  font-family: Lato;
  font-size: 18px;
  margin-top: 2%;
}
@media screen and (max-width: 768px) {
  .contact-full-page-header {
    font-size: 30px;
  }
  .contact-full-page-sub-header {
    font-size: 11px;
  }

  .contact-full-page-father {
    width: 100px;
    height: 100px;
  }

  .left-image,
  .right-image {
    height: 30vh;
  }
  .post,
  .name {
    font-size: 15px;
  }
}
