.car-image-scroller {
  display: flex;
  flex-direction: column;
}

.car-general-detail-wrapper {
  display: flex;
  /* width: 100%; */
  justify-content: center;
  align-items: center;
}
.car-image-indicator {
  overflow: hidden;
  height: 100px;
  width: 50%;
  display: flex;
  align-self: center;
  justify-content: flex-start;
}

.car-image-indicator ul {
  transition: 0.3s ease;
  display: flex;
  cursor: pointer;
}
.car-image-indicator-li {
  width: 100px;
  height: 100%;
  transform: scale(0.9);
  background-position: center;
  background-size: contain;
  opacity: 0.5;
  background-repeat: no-repeat;
}
.car-first-list,
.car-second-list {
  display: flex;
  justify-content: space-around;
  width: 100%;
  /* margin-top: 2%; */
}
.car-second-list {
  margin-top: 10%;
}
.car-first-list li,
.car-second-list li {
  flex-basis: 100%;
  text-align: center;
  /* margin-top: 2%; */
}
.car-image-pop-up {
  position: absolute;
  background-color: rgb(0, 0, 0, 0.9);
  height: 100vh;
  top: 0;
  width: 100%;
  /* height: max-content; */
  overflow: auto;
  /* scroll-behavior: smooth; */
}
.pop-up-image {
  background-image: url(https://autos-motos.net/pictures/BE478268990/14309/image-01-B~v1622644578.JPG);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  min-width: 50%;
  min-height: 100%;
  transform: scale(0.95);
  transition: 0.3s ease;
}
.cross-car-pop-up {
  cursor: pointer;
  background-image: url(../images/cross.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 1.5%;
  height: 5%;
  float: right;
  margin-right: 17px;
  margin-top: 7px;
  transition: 0.3s ease;
  position: fixed;
  right: 15px;
}
.close-image-pop-up {
  display: none;
  animation: fade 4s ease;
}
@keyframes fade {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    display: none;
  }
}
.car-basic-info-container {
  display: flex;
  justify-content: center;
  width: 50%;
  margin-top: 2%;
  align-items: center;
}
.car-basic-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin-top: 20px; */
  padding-top: 10px;
  /* box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25); */
  box-shadow: 0px 2px rgb(185, 185, 185);
  padding-bottom: 10px;
  /* background-color: #f5f5f5; */
  /* padding: 10px; */
  font-size: 20px;
  font-family: Lato;
  font-weight: 900;
  /* border-radius: 5px; */
}
.car-general-details-container,
.car-technical-details-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;

  /* margin-right: 5%; */
}
.car-general-details-heading,
.car-technical-details-heading {
  margin-top: 10%;
  font-size: 25px;
  font-weight: 900;
  align-self: start;
  margin-bottom: 7%;
  font-family: Lato;
}

.car-date-info,
.car-km-info,
.car-petrol-info,
.car-gear-info,
.car-pollution-info {
  font-size: 16px;
  font-family: Lato;
  font-weight: 700;
  margin-top: 5%;
}
.car-name span {
  color: rgb(80, 80, 80);
  font-size: 15px;
  font-weight: 400;
}
.car-date-heading,
.car-km-heading,
.car-petrol-heading,
.car-gear-heading,
.car-pollution-heading {
  font-style: Lato;
  color: rgb(80, 80, 80);
  font-size: 14px;
}
.car-date-image,
.car-km-image,
.car-petrol-image,
.car-gear-image,
.car-pollution-image {
  background-image: url(../images/calendar.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  /* min-width: 50%;
  min-height: 100%; */
  height: 20px;
}
.car-km-image {
  background-image: url(../images/km.png);
}
.car-petrol-image {
  background-image: url(../images/petrol.png);
}

.car-gear-image {
  background-image: url(../images/gear.png);
}

.car-pollution-image {
  background-image: url(../images/pollution.png);
}

.car-general-details-box,
.car-technical-details-box {
  /* box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
  width: 50%;
  /* height: 50vh; */
  /* background-color: #f5f5f5; */
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
}
.car-first-line,
.car-second-line,
.car-third-line {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

