.car-date-km {
  border-left: 1px solid;
  border-right: 1px solid;
}
.car-petrol-box {
  border-right: 1px solid;
}

.engine-container,
.condition-container,
.features-container {
  width: 100%;
}
.engine-info,
.condition-info,
.features-info {
  display: flex;
  flex-direction: column;
  margin-bottom: 5%;
}
.features-info {
  flex-direction: row;
}
.engine-first-list,
.engine-second-list,
.condition-first-list,
.condition-second-list {
  margin-top: 2%;
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.engine-first-list li,
.engine-second-list li,
.condition-first-list li,
.condition-second-list li {
  flex-basis: 100%;
  text-align: center;
}
.feature-list {
  flex-basis: 100%;
  font-family: Lato;
  font-size: 14px;
  font-weight: 700;
}
.feature-list li {
  margin-bottom: 2%;
}
.engine-heading,
.condition-heading,
.features-heading {
  font-size: 20px;
  font-family: "Lato";
  font-weight: 700;
  margin-bottom: 4%;
  padding: 5px;
  background-color: rgb(234 234 234);
}
.feature-list > li::before {
  content: "\2022";
  /* color: #7d7d7d; */
  color: #6ddb46;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: 1em;
}
.key {
  color: #7d7d7d;
  /* padding-left: 30%; */
}
.value {
  font-weight: 700;
  margin-top: 2%;
  /* font-size: ; */
}
.key,
.value {
  font-family: "Lato";
  /* flex-basis: 100%; */
}
@media screen and (max-width: 1100px) {
  .car-image-indicator {
    width: 73%;
  }
}
@media screen and (max-width: 768px) {
  .car-image-indicator {
    width: 90%;
  }
  .car-image-pop-up {
    z-index: 4;
  }
  .car-date-info,
  .car-km-info,
  .car-petrol-info,
  .car-gear-info,
  .car-pollution-info {
    margin-top: 15%;
    font-size: 13px;
  }
  .cross-car-pop-up {
    width: 4.5%;
    height: 6%;
    z-index: 5;
  }
  .pop-up-image {
    min-height: 50%;
    background-size: 100%;
    transform: scale(0.95);
  }

  .scroll-car-wrapper {
    height: 35vh;
  }

  .car-basic-info {
    font-size: 12px;
  }
  .car-general-details-heading,
  .car-technical-details-heading {
    margin-bottom: 15%;
  }
  .engine-heading,
  .condition-heading,
  .features-heading {
    margin-bottom: 6%;
  }
  .engine-info,
  .condition-info {
    margin-bottom: 15%;
  }
  .feature-list {
    font-size: 13px;
  }
  .car-first-list {
    margin-bottom: 5%;
  }
  .car-date-info,
  .car-km-info,
  .car-petrol-info,
  .car-gear-info,
  .car-pollution-info {
    font-size: 12px;
  }
  .car-date-info,
  .car-km-info,
  .car-petrol-info,
  .car-gear-info,
  .car-pollution-info {
    font-size: 14px;
  }
  .detail-active {
    font-size: 15px;
  }
  .engine,
  .condition,
  .feature {
    font-size: 14px;
  }
  .car-general-details-box,
  .car-technical-details-box,
  .car-basic-info {
    width: 100%;
    box-shadow: none;
  }
  .scroll-card-container {
    height: 35vh;
  }
  .car-basic-info-container {
    width: 100%;
  }
  .car-general-details-container {
    width: 90%;
  }
  .key {
    font-size: 10px;
  }
  .value {
    font-size: 13px;
  }
}
